  methods: {
    initG1: function() {
      this.trxs.push(TRXS[0])
      this.trxs.push(TRXS[1])
      this.trx_idx = 1

      var now = 1596207600000
      for (var idx = 0; idx < COUNT; idx++ ) {
        var date = new Date(now + (GAP * idx))
        
        this.g1_wind_option.series[0].data.push({
          name: date.toString(),
          value: [
            timestamp(date),
            WIND1[idx]]
        })

        this.g1_power_option.series[0].data.push({
          name: date.toString(),
          value: [
            timestamp(date),
            POWER1[idx] / 1000]
        })

        this.g1_wind_idx = idx
      }

      this.g1_wind_interval = setInterval(() => {
        this.g1_wind_idx++
        var date = new Date(now + (GAP * this.g1_wind_idx))
        
        this.g1_wind_option.series[0].data.shift()
        this.g1_wind_option.series[0].data.push({
          name: date.toString(),
          value: [
            timestamp(date),
            WIND1[this.g1_wind_idx % WIND1.length]]
        })
        this.g1_wind.setOption(this.g1_wind_option);

        this.g1_power_option.series[0].data.shift()
        this.g1_power_option.series[0].data.push({
          name: date.toString(),
          value: [
            timestamp(date),
            POWER1[this.g1_wind_idx % POWER1.length] / 1000]
        })
        this.g1_power.setOption(this.g1_power_option);
      }, INTERVAL)

      this.g1_guage_interval = setInterval(() => {
        this.trx_idx++
        if (this.trx_idx >= 100) this.trx_idx = 0
        this.trxs.shift()
        this.trxs.push(TRXS[this.trx_idx])
        var p = (this.g1_power_option.series[0].data[this.g1_power_option.series[0].data.length - 1].value[1])
        this.g1_guage_option.series[0].data[0].value = (p * (Math.random() * (1.05 - 0.95) + 0.95)).toFixed(1)
        this.g1_guage.setOption(this.g1_guage_option);
      }, 250)
    },
    initG2: function() {
      var now = 1596207600000
      for (var idx = 0; idx < COUNT; idx++ ) {
        var date = new Date(now + (GAP * idx))
        
