        this.g2_power_option.series[0].data.shift()
        this.g2_power_option.series[0].data.push({
          name: date.toString(),
          value: [
            timestamp(date),
            POWER2[this.g2_wind_idx % POWER2.length] / 1000]
        })
        this.g2_power.setOption(this.g2_power_option);
      }, INTERVAL)

      this.g2_guage_interval = setInterval(() => {
        
        var p = (this.g2_power_option.series[0].data[this.g2_power_option.series[0].data.length - 1].value[1])
        this.g2_guage_option.series[0].data[0].value = (p * (Math.random() * (1.05 - 0.95) + 0.95)).toFixed(1)
        this.g2_guage.setOption(this.g2_guage_option);
      }, 250)
    },
    initG3: function() {
